import * as THREE from "three";
import { fbxSource } from "./App";

let FBXLoader = require("three-fbxloader-offical");

export const applyMaterial = (materials, newMaterial) => {
    if (Array.isArray(materials)) {
        return materials.map(() => {
            return newMaterial;
        });
    } else {
        return newMaterial;
    }
};

export const convertMaterial = materials => {
    if (Array.isArray(materials)) {
        return materials.map(m => {
            return new THREE.MeshBasicMaterial({
                map: m.map,
                skinning: true,
            });
        });
    } else {
        return new THREE.MeshBasicMaterial({
            map: materials.map,
            skinning: true,
        });
    }
};

export const addModel = modelDetails => {
    const {
        loaderCallback,
        model,
        texture,
        tag,
        flipped,
        handleLoad,
        handleError,
    } = modelDetails;
    if (model) {
        let loader = new FBXLoader();
        loader.load(
            model,
            object => {
                loaderCallback(object, texture, tag, flipped);
            },
            s => {
                handleLoad(s);
            },
            error => {
                handleError(error);
            }
        );
    }
};

export const analyzeWeaponCode = code => {
    const flipped = code[code.length - 1] === "b";
    const weaponLeft = code.substring(0, code.length - 1);
    const model = `${fbxSource}/fbx/${weaponLeft}/${weaponLeft}.fbx`;
    const texture = `${fbxSource}/fbx/${weaponLeft}/${weaponLeft}.png`;

    return { model, texture, flipped };
};

export const disposeItem = item => {
    item.traverse(child => {
        if (child.isMesh) {
            if (Array.isArray(child.material)) {
                child.material.forEach(mat => {
                    mat.map.dispose();
                    mat.dispose();
                });
            } else {
                child.material.map.dispose();
                child.material.dispose();
            }
            child.geometry.dispose();
        }
    });
};
