import React, { Component } from "react";
import * as THREE from "three";
import { fbxSource } from "./App";
import { CAM_PARAMS } from "./consts";
import {
    applyMaterial,
    convertMaterial,
    addModel,
    analyzeWeaponCode,
    disposeItem,
} from "./viewerHelpers";

let OrbitControls = require("three-orbit-controls")(THREE);
let FBXLoader = require("three-fbxloader-offical");

const reloadKeys = [
    "model",
    "weaponLeft",
    "weaponRight",
    // "faceOffset",
    "faceTexture",
];
const shouldReloadModel = (nextProps, currentProps) => {
    return reloadKeys.some(key => nextProps[key] !== currentProps[key]);
};

class ModelViewer extends Component {
    checkProps = () => {
        this.cameraPosition = this.props.cameraPosition || {
            x: 2,
            y: 0,
            z: 10,
        };
        this.controlsPosition = this.props.controlsPosition || {
            x: 0,
            y: 0,
            z: 0,
        };
        this.viewport = this.props.viewport || {
            width: window.innerWidth,
            height: window.innerHeight,
        };
    };

    animate = () => {
        this.animation = requestAnimationFrame(this.animate);
        if (this.mixers.length > 0) {
            const d = this.clock.getDelta();
            for (var i = 0; i < this.mixers.length; i++) {
                this.mixers[i].update(d);
            }
        }
        this.renderer.render(this.scene, this.camera);
    };

    addAnimation = (object, animationIdx) => {
        object.mixer = new THREE.AnimationMixer(object);
        if (object.mixer) {
            this.mixers.push(object.mixer);
        }
        if (animationIdx !== undefined) {
            if (animationIdx.includes("+")) {
                const args = animationIdx.split("+");
                if (args.length === 2) {
                    const aniFile = args[0];
                    const aniName = args[1];
                    const ani = `${fbxSource}/fbx/${aniFile}.fbx`;
                    let aniLoader = new FBXLoader();
                    aniLoader.load(ani, obj => {
                        const animation = obj.animations.find(
                            a => a.name === aniName
                        );
                        if (animation) {
                            let action = object.mixer.clipAction(animation);
                            action.play();
                        }
                    });
                }
            } else if (object.animations && object.animations[animationIdx]) {
                let action = object.mixer.clipAction(
                    object.animations[animationIdx]
                );
                action.play();
            }
        }
    };

    mainLoaderCallback = (object, textureFile) => {
        let weaponLeft, weaponRight;
        if (this.props.weaponLeft !== "") {
            this.currentWeapons["weaponLeft"] = this.models[
                "weaponLeft"
            ].clone();
            weaponLeft = this.currentWeapons["weaponLeft"];
        }
        if (this.props.weaponRight !== "") {
            this.currentWeapons["weaponRight"] = this.models[
                "weaponRight"
            ].clone();
            weaponRight = this.currentWeapons["weaponRight"];
        }
        let material = null;
        if (textureFile !== null) {
            const texture = new THREE.TextureLoader().load(textureFile);
            material = new THREE.MeshBasicMaterial({
                map: texture,
                skinning: true,
            });
        }

        object.traverse(child => {
            // if (child.name.includes("Eff")) {
            //     console.log("Eff", child.name);
            //     child.visible = false;
            // }
            if (child.isMesh) {
                if (material !== null) {
                    child.material = applyMaterial(child.material, material);
                } else {
                    child.material = convertMaterial(child.material);
                }
                child.castShadow = false;
                child.receiveShadow = false;
            }
        });

        this.changeFace(object, this.props.faceOffset);
        // Add weapons
        if (this.props.weaponLeft !== "" || this.props.weaponRight !== "")
            object.traverse(child => {
                if (
                    this.props.weaponLeft !== "" &&
                    child.name === "jWeaponL" &&
                    child.children.length === 0
                ) {
                    child.add(weaponLeft);
                }
                if (
                    this.props.weaponRight !== "" &&
                    child.name === "jWeaponR" &&
                    child.children.length === 0
                ) {
                    child.add(weaponRight);
                }
            });
        if (this.props.rotation) {
            object.rotation.x = this.props.rotation.x;
            object.rotation.y = this.props.rotation.y;
            object.rotation.z = this.props.rotation.z;
        }
        this.models["main"] = object;
        this.addAnimation(object, this.props.animationIdx);
        this.scene.add(object);
    };

    weaponLoaderCallback = (object, textureFile, tag, flipped) => {
        let material = null;
        if (textureFile !== null) {
            const texture = new THREE.TextureLoader().load(textureFile);
            material = new THREE.MeshBasicMaterial({
                map: texture,
                skinning: true,
            });
        }
        object.traverse(function (child) {
            // if (child.name.includes("Eff")) {
            //     console.log("Eff", child.name);
            //     child.visible = false;
            // }
            if (child.isMesh) {
                if (material !== null) {
                    child.material = applyMaterial(child.material, material);
                } else {
                    child.material = convertMaterial(child.material);
                }
                child.castShadow = false;
                child.receiveShadow = false;
            }
        });
        if (flipped) {
            object.rotation.y = Math.PI;
        }

        this.models[tag] = object;
    };

    handleLoad = e => {
        // console.log(`Loaded ${e.loaded}/${e.total}`);
        if (this.props.onLoad) {
            this.props.onLoading(e);
        }
    };

    handleError = e => {
        if (this.props.onError) {
            this.props.onError(e);
        }
    };

    addWeapon = (side, code) => {
        const { model, texture, flipped } = analyzeWeaponCode(code);
        const modelToAdd = {
            loaderCallback: this.weaponLoaderCallback,
            model,
            texture,
            flipped,
            tag: `weapon${side}`,
            handleLoad: this.handleLoad,
            handleError: this.handleError,
        };
        addModel(modelToAdd);
    };

    removeWeapon = side => {
        const boneName = `jWeapon${side[0]}`;
        this.models["main"].traverse(child => {
            if (child.name === boneName && child.children.length === 1) {
                if (child.children[0].type === "Group") {
                    child.traverse(ch => {
                        if (ch.isMesh) {
                            disposeItem(ch);
                        }
                    });
                    child.remove(this.currentWeapons[`weapon${side}`]);
                    disposeItem(this.currentWeapons[`weapon${side}`]);
                    disposeItem(this.models[`weapon${side}`]);
                    this.models[`weapon${side}`] = null;
                }
            }
        });
    };

    updateWeapons = nextProps => {
        ["Right", "Left"].forEach(side => {
            if (nextProps[`weapon${side}`] !== this.props[`weapon${side}`]) {
                if (this.props[`weapon${side}`] !== "") {
                    this.removeWeapon(side);
                }
                if (nextProps[`weapon${side}`] !== "") {
                    this.addWeapon(side, nextProps[`weapon${side}`]);
                }
            }
        });
    };

    changeFace = (object, faceOffset) => {
        let faceMaterial;
        object.traverse(child => {
            if (child.name === "mBodyAll") {
                const faceOverride =
                    this.props.texture !== this.props.faceTexture;
                if (faceOverride) {
                    const faceTexture = new THREE.TextureLoader().load(
                        this.props.faceTexture
                    );
                    faceMaterial = new THREE.MeshBasicMaterial({
                        map: faceTexture,
                        skinning: true,
                    });
                }
                if (faceOffset && child.geometry.groups.length === 3) {
                    const faceGroups = child.geometry.groups.filter(
                        k => k.count < 1000
                    );
                    if (faceGroups.length === 2) {
                        if (faceOverride && faceMaterial) {
                            const matIndex = faceGroups.map(
                                group => group.materialIndex
                            );
                            matIndex.forEach(i => {
                                child.material[i] = faceMaterial;
                            });
                        }
                        const start = Math.min(
                            faceGroups[0].start,
                            faceGroups[1].start
                        );
                        const end = Math.max(
                            faceGroups[0].start + faceGroups[0].count,
                            faceGroups[1].start + faceGroups[1].count
                        );
                        for (let i = start; i < end; i += 1) {
                            child.geometry.attributes.uv.array[i * 2] +=
                                0.25 * faceOffset.x;
                            child.geometry.attributes.uv.array[i * 2 + 1] +=
                                0.25 * faceOffset.y;
                        }
                    }
                }
                child.geometry.uvsNeedsUpdate = true;
            }
        });
    };

    init = () => {
        this.models = {};
        this.currentWeapons = {};
        // mixers
        this.mixers = [];
        this.clock = new THREE.Clock();

        // Scene
        this.scene = new THREE.Scene();
        this.scene.background = new THREE.Color(this.props.bgColor);

        // Camera
        this.camera = new THREE.PerspectiveCamera(
            CAM_PARAMS.angle,
            this.viewport.width / this.viewport.height,
            CAM_PARAMS.near,
            CAM_PARAMS.far
        );
        this.camera.position.set(
            this.cameraPosition.x,
            this.cameraPosition.y,
            this.cameraPosition.z
        );
        this.camera.updateProjectionMatrix();

        // Controls
        this.controls = new OrbitControls(this.camera, this.container);
        this.controls.target.set(
            this.controlsPosition.x,
            this.controlsPosition.y,
            this.controlsPosition.z
        );
        this.controls.update();

        // Light
        let light = new THREE.DirectionalLight(0xffffff);
        light.position.set(0, 200, 0);
        this.scene.add(light);

        // model
        // Add right weapon
        if (this.props.weaponRight !== "") {
            this.addWeapon("Right", this.props.weaponRight);
        }
        // Add left weapon
        if (this.props.weaponLeft !== "") {
            this.addWeapon("Left", this.props.weaponLeft);
        }
        // Add main model
        const modelToAdd = {
            loaderCallback: this.mainLoaderCallback,
            model: this.props.model,
            texture: this.props.texture,
            tag: "main",
            handleLoad: this.handleLoad,
            handleError: this.handleError,
        };
        addModel(modelToAdd);

        // Renderer
        this.renderer = new THREE.WebGLRenderer({
            antialias: false,
            alpha: true,
        });
        this.renderer.setClearColor(0x000000, 0);
        this.renderer.setSize(this.viewport.width, this.viewport.height);
        this.container.appendChild(this.renderer.domElement);
        this.animate();
    };

    componentDidMount() {
        this.checkProps();
        this.init();
    }

    shouldComponentUpdate(nextProps) {
        // Update viewport
        if (nextProps.viewport !== this.props.viewport) {
            const { width, height } = nextProps.viewport;
            this.renderer.setSize(width, height);
            this.camera.aspect = width / height;
            this.camera.updateProjectionMatrix();
        }
        // Update face
        if (nextProps.faceOffset !== this.props.faceOffset) {
            this.changeFace(this.models.main, nextProps.faceOffset);
        }

        // Update animation
        if (nextProps.animationIdx !== this.props.animationIdx) {
            this.models.main.mixer.stopAllAction();
            this.mixers = [];
            this.addAnimation(this.models["main"], nextProps.animationIdx);
        }
        // Update background color
        if (nextProps.bgColor !== this.props.bgColor) {
            this.scene.background = new THREE.Color(nextProps.bgColor);
        }
        // Update weapons
        this.updateWeapons(nextProps);

        // Update model
        if (shouldReloadModel(nextProps, this.props)) {
            if (this.props.model !== "") {
                // stop current animation
                this.models.main.mixer.stopAllAction();
                // remove old model
                this.scene.remove(this.models.main);
                disposeItem(this.models.main);
                this.models.main = null;
                // remove old mixers
                this.mixers = [];
            }
            // Add new model
            const modelToAdd = {
                loaderCallback: this.mainLoaderCallback,
                model: nextProps.model,
                texture: nextProps.texture,
                tag: "main",
                handleLoad: this.handleLoad,
                handleError: this.handleError,
            };
            addModel(modelToAdd);
        }
        return false;
    }

    componentWillUnmount() {
        cancelAnimationFrame(this.animation);
        this.mixers = null;
        this.clock = null;
        this.camera = null;
        this.controls = null;
        this.scene = null;
        this.renderer = null;
    }
    render() {
        return (
            <div
                ref={el => {
                    this.container = el;
                }}
            />
        );
    }
}

export default ModelViewer;
